import 'simplyview'
import 'simplyflow'
import webid from '../components/solid-webid.mjs'
import solidFS from '../components/solid-fs.mjs'

const podlist = simply.app({
	state: simply.state.signal({
		path: '/',
		list: [],
		solidConfiguration: {

		}
	}),
	hooks: {
		start: async function() {
			simply.bind({
				root: this.state
			})
		}
	},
	components: {
		webid,
		solidFS
	}
})
podlist.start()

globalThis.podlist = podlist